// Worlds
.Page {
    margin: 0;
    padding: 0;
}

.PageBody {
    min-width: 320px;
    
    margin: 0;
    padding: 0;
}

.Main {
    margin: 0;
    padding: 0; 
}

*, *:before, *:after {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
}
  
.VisuallyHidden {
position: absolute;
width: 1px;
height: 1px;
margin: -1px;
border: 0;
padding: 0;
white-space: nowrap;
clip-path: inset(100%);
clip: rect(0 0 0 0);
overflow: hidden;
}